

import { dsBridge } from '../globalVar';

const envValue = window.location.href.split('?')[1];
/**
 * ----
 * @param {object} obj  
 * {"courseData":"[]"}
 */
function courseLoaded(obj) {
    //
    console.log(obj);
    console.log(JSON.stringify({"type": "loaded"}))

    console.log('----');

    if (envValue === 'debug') {
        console.warn('--------->  ');
    } else {
        dsBridge.call('echo.courseLoaded', obj);

    }
}

export {
    courseLoaded
}


